*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Colaborate-Medium", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1450px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Museo Sans", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 4.8rem;
}

h3 {
  font-size: 2.4rem;
}

p {
  font-size: 2rem;
  line-height: 1.8;
}

/** Media Queries **/
.btn-enlace {
  background-color: #EBA904;
  color: #FFFFFF;
  display: block;
  padding: 1rem;
  text-align: center;
  text-transform: uppercase;
  margin: 1rem 0;
  font-weight: 700;
  font-family: "Museo Sans", sans-serif;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .btn-enlace {
    display: inline-block;
    background-color: unset;
    color: #000;
    position: relative;
    transition-property: color;
    transition-duration: 300ms;
  }
  .btn-enlace::after {
    background-color: #EBA904;
    content: "";
    position: absolute;
    left: 1rem;
    bottom: 0.5rem;
    height: 0.3rem;
    width: 3rem;
    transition-property: width;
    transition-duration: 300ms;
  }
  .btn-enlace:hover {
    color: #EBA904;
  }
  .btn-enlace:hover::after {
    width: 80%;
  }
}

.envio {
  background-color: #EBA904;
  padding: 1rem;
}
.envio__heading {
  margin: 0;
  text-align: center;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
}

.barra {
  border-bottom: 0.1rem solid #e6e6e6;
  padding: 1rem 0;
}
.barra__logo {
  margin-top: 1.5rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .barra__logo {
    margin: 0;
    height: 2.3rem;
  }
}
@media (min-width: 768px) {
  .barra .contenedor {
    display: flex;
    justify-content: space-between;
    padding: 2rem 5rem;
    align-items: center;
  }
}

.user-mincart {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 1.5rem;
  align-items: center;
}
@media (min-width: 768px) {
  .user-mincart {
    margin-top: 0;
  }
}
.user-mincart__icono {
  width: 3rem;
}

.navegacion {
  display: none;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    margin: 0 1rem;
  }
}
@media (min-width: 768px) and (min-width: 1024px) {
  .navegacion {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .navegacion__enlace {
    color: #000;
    font-weight: 400;
    font-size: 1.6rem;
    transition-property: color;
    transition-duration: 200ms;
  }
  .navegacion__enlace:hover {
    color: #EBA904;
  }
}

.promo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .promo {
    display: block;
    margin-top: 5rem;
  }
  .promo__imagen {
    margin: 0 auto;
  }
}

@media (min-width: 768px) {
  .promo-banner {
    display: grid;
    grid-template-columns: 3fr 2fr;
    margin-top: 5rem;
  }
}
.promo-banner__imagen {
  background-image: url("../img/ecko-landing 2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  height: 20rem;
}
@media (min-width: 768px) {
  .promo-banner__imagen {
    height: 40rem;
  }
}

.info-pack {
  background: #020024;
  background: radial-gradient(circle, #020024 0%, #00d4ff 0%, #0076be 100%);
  margin-top: 3rem;
  padding: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .info-pack {
    display: flex;
    margin-top: 0;
    flex-direction: column;
    text-align: left;
    justify-content: center;
  }
}
.info-pack__contenedor {
  padding: 3rem;
}
@media (min-width: 768px) {
  .info-pack__contenedor {
    max-width: 50%;
    margin: 0 auto;
  }
}
.info-pack__info .info-pack__texto {
  border-bottom: 0.1rem solid #e6e6e6;
}
.info-pack__heading, .info-pack__texto {
  color: #FFFFFF;
  font-weight: 400;
  font-family: "Museo Sans", sans-serif;
  margin: 0;
  max-width: 80rem;
}
.info-pack__heading {
  font-size: 1.8rem;
  display: block;
}
@media (min-width: 768px) {
  .info-pack__heading {
    font-size: 3rem;
  }
}
.info-pack__texto {
  font-size: 1.4rem;
  display: inline-block;
}
@media (min-width: 768px) {
  .info-pack__texto {
    font-size: 2.2rem;
  }
}

.contenedor-receta {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .contenedor-receta {
    flex-direction: row;
    align-items: center;
    gap: 5rem;
  }
}

.receta__heading {
  font-size: 2.8rem;
  font-family: "Museo Sans", sans-serif;
  margin-bottom: 1rem;
  text-align: center;
}
@media (min-width: 768px) {
  .receta__heading {
    margin: 5rem 0;
    text-align: left;
    font-size: 3.4rem;
  }
}
.receta__subheading {
  font-size: 2rem;
  font-family: "Museo Sans", sans-serif;
  font-weight: 400;
  text-align: center;
}
@media (min-width: 768px) {
  .receta__subheading {
    margin: 6rem 0;
    text-align: left;
    font-size: 2.4rem;
  }
}
.receta__texto {
  margin: 1rem;
  font-size: 1.6rem;
}
.receta__texto:first-of-type {
  margin-bottom: 1rem;
}
.receta__texto:last-of-type {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .receta__texto {
    max-width: 100%;
  }
}
@media (min-width: 1024px) {
  .receta__texto {
    max-width: 80%;
  }
}

.mas-vendidos {
  margin-top: 5rem;
  text-align: center;
}
.mas-vendidos__header {
  margin: 2rem;
  position: relative;
}
@media (min-width: 768px) {
  .mas-vendidos__header .flechas {
    position: absolute;
    top: 40%;
    right: 0;
  }
}
.mas-vendidos__heading {
  font-size: 3.5rem;
  margin: 0;
  font-weight: 700;
  line-height: 1;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .mas-vendidos__heading {
    margin-bottom: 0;
  }
}

.articulo {
  padding: 2rem;
}
@media (min-width: 768px) {
  .articulo {
    padding: 2rem;
  }
}
.articulo__imagen {
  width: 100%;
  margin: 0 auto;
}
.articulo__heading {
  font-size: 2.5rem;
  margin-top: 1rem;
  color: #666666;
  font-weight: 700;
  font-family: "Museo Sans", sans-serif;
  margin-bottom: 0;
  text-align: left;
}
.articulo__texto {
  font-size: 1.4rem;
  margin: 0.5rem 0;
  max-width: 30rem;
  text-align: left;
}
.articulo .btn-enlace {
  display: inline-block;
}

.slick-dots {
  list-style: none;
  display: flex;
  gap: 1rem;
  justify-content: center;
}
.slick-dots button {
  border: none;
  border-radius: 50%;
  background-color: #e6e6e6;
  color: #e6e6e6;
  width: 1.5rem;
  height: 1.5rem;
  font-size: 0.1rem;
}

.slick-active button {
  background-color: #EBA904;
  color: #EBA904;
  font-size: 0.1rem;
}

.flecha-atras,
.flecha-siguiente {
  border: none;
  background-color: unset;
  font-family: "Museo Sans", sans-serif;
  font-weight: 700;
  color: #EBA904;
  border-radius: 50%;
  border: 0.1rem solid #EBA904;
  width: 3rem;
  height: 3rem;
  text-align: center;
  transition-property: background-color color;
  transition-duration: 300ms;
}
.flecha-atras:hover,
.flecha-siguiente:hover {
  cursor: pointer;
  background-color: #EBA904;
  color: #FFFFFF;
}

.flecha-atras {
  margin-right: 1rem;
}

.acordeon {
  width: 40rem;
  margin-top: 7rem;
  margin-left: auto;
  margin-right: auto;
}
.acordeon__menu {
  width: 40rem;
}
.acordeon__lista {
  width: 100%;
  padding: 0;
  margin-top: 0;
}
.acordeon__item {
  width: 100%;
  list-style: none;
  height: 4rem;
  overflow: hidden;
  border-bottom: 0.1rem solid #FFFFFF;
  transition: 300ms;
}
.acordeon__heading {
  display: block;
  padding: 1.5rem;
  text-decoration: none;
  font-family: sans-serif;
  color: #000;
  display: flex;
  justify-content: space-between;
  font-weight: 700;
  font-size: 2rem;
}
.acordeon__texto {
  color: #000;
  font-size: 1.2rem;
  padding: 0 2rem;
}

.fa-chevron-down {
  transition: 300ms;
}

.scale {
  height: 180px;
}

.indicador {
  font-size: 3rem;
}